import React, { useEffect, useState } from 'react'
import './index.css'
import Store from '../../Store';
import _ from 'lodash'
import subscribeHook from '../../DefineHook/subscribe';
import { Select, Input } from 'antd';

const attributeValueMap = {
  attributeValue: '',
  size: '',
  disabled: '',
  addonBefore: '',
  addonAfter: '',
  placeholder: '',
  url: 'URL',
  openType: '',
  listItemNum: '',
  title: '',
  buttonType: '',
  size: '',
  danger: '',
  ghost: '',
  prefix: '',
  suffix: '',
  allowClear: '',
  showCount: '',
  checked: ''
}


export default function RightCom(props) {

  const {rightPanel,comId} = props;
  const attributeMapRight = _.cloneDeep(Store.getState().attributeMap)
  const [update,setUpdate] = useState({})

  subscribeHook(() => {
    setUpdate({})
  })

  const getAttributeValueCom = (item,index) => {
    switch (item) {
      case 'openType': {
        return <select onChange={onChange(item)}>
          <option value={false}></option>
          <option value={true} selected></option>
        </select>
      }
      case 'disabled': {
        return <Select 
          style={{ width: 120,height: 25 }}
          defaultValue={false}
          onChange={onChange(item)}
          options={
          [
            { label: '', value: true },
            { label: '', value: false }
          ]
        } />
      }
      case 'checked': {
        return <Select 
          style={{ width: 120,height: 25 }}
          defaultValue={false}
          onChange={onChange(item)}
          options={
          [
            { label: '', value: true },
            { label: '', value: false }
          ]
        } />
      }
      case 'danger': {
        return <Select 
          style={{ width: 120,height: 25 }}
          defaultValue={false}
          onChange={onChange(item)}
          options={
          [
            { label: '', value: true },
            { label: '', value: false }
          ]
        } />
      }
      case 'ghost': {
        return <Select 
          style={{ width: 120,height: 25 }}
          defaultValue={false}
          onChange={onChange(item)}
          options={
          [
            { label: '', value: true },
            { label: '', value: false }
          ]
        } />
      }
      case 'allowClear': {
        return <Select 
          style={{ width: 120,height: 25 }}
          defaultValue={false}
          onChange={onChange(item)}
          options={
          [
            { label: '', value: true },
            { label: '', value: false }
          ]
        } />
      }
      case 'showCount': {
        return <Select 
          style={{ width: 120,height: 25 }}
          defaultValue={false}
          onChange={onChange(item)}
          options={
          [
            { label: '', value: true },
            { label: '', value: false }
          ]
        } />
      }
      case 'buttonType': {
        return <Select 
          style={{ width: 120,height: 20 }}
          defaultValue='primary'
          onChange={onChange(item)}
          options={
          [
            { label: '', value: 'primary' },
            { label: '', value: 'dashed' },
            { label: '', value: 'text' },
            { label: '', value: 'link' }
          ]
        } />
      }
      case 'size': {
        return <Select 
          style={{ width: 120,height: 25 }}
          defaultValue='default'
          onChange={onChange(item)}
          options={
          [
            { label: '', value: 'large' },
            { label: '', value: 'small' },
            { label: '', value: 'default' },
          ]
        } />
      }
      case 'listItemNum': {
        return <Input style={{ width: 120,height: 25 }} type={'number'} key={index} onChange={onChange(item)} value={attributeMapRight?.[comId]?.[item] || ''}></Input>
      }
      default: {
        return <Input style={{ width: 120, height: 25 }} key={index} onChange={onChange(item)} value={attributeMapRight?.[comId]?.[item] || ''}></Input>
      }
    }
  }

  const onChange = (name) => {
    return (value) => {
      if(!attributeMapRight[comId]){
        attributeMapRight[comId] = {}
      }
      if(typeof value === 'object'){
        value = value.target.value
      }
      attributeMapRight[comId][name] = value;
      Store.dispatch({type:'change',attributeMap: attributeMapRight})
    }
  }

  return (
    <div className='rightCom'>
      <div style={{marginTop:'40px'}}>
        {comId}
        <hr></hr>
        {
          (rightPanel[comId] || []).map((item,index) => {
            return <div key={index} className='attributeItem'>
              <label>{attributeValueMap[item]}</label>
              <div className='attributeItemValue'>
                {getAttributeValueCom(item,index)}
              </div>
            </div>
          })
        }
      </div>
    </div>
  )
}
